<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
      </div>
    <div class='tap'>
      <HorizontalTap class='center' :data='data2' :active="3" style="padding-left: 100px;"></HorizontalTap>
    </div>
    <div v-if="pdfFlg">
      <!--<i class="el-icon-error close-btn"  @click="closePdf" ></i>-->
      <PDF v-if="pdfFlg" :pdf-url="pdfUrl" :is-contrast="pdfIsContrast" :display-id="displayId"></PDF>
    </div>
    <div class="col-md-10 col-md-offset-1 mt-6">
      <el-tabs v-model="activeName" type="border-card" class="mb-4">
        <el-tab-pane name="project">
          <span slot="label"><i class="el-icon-connection"></i> 项目相关指导材料</span>
          <div class="row mb-5">
            <div class="col-md-12 col-xs-12 pl-6 mt-6">
              <div class="row">
                <span class="line"></span><span>学习视频</span>
              </div>
              <div class="row mt-6">
                <div class="col-md-3 text-center video-item" v-for="(item, key) in videoList" v-bind:key="key" @click="gotoVideo(item)">
                  <img class="video-img" :src="item.materialImg" style="border: 1px solid #666!important;" :style="{ height: sizeChange(150)}">
                  <span class="video-name"><a class="font-small">{{item.materialName}}</a></span>
                  <i class="iconfont iconshipinjiaocheng font-bold video-icon"></i>
                </div>
              </div>
              <div class="row mt-6">
                <span class="line"></span><span>文档模板</span>
              </div>
              <div class="row mt-6">
                <div class="col-md-2 text-center mr-6" v-for="(item, key) in docList" v-bind:key="key">
                  <img class="video-img pointer" src="../../../../assets/img/word.jpg" :style="{ height: sizeChange(150)}" @click="gotoPDF(item)">
                  <a class="video-name font-small">{{item.materialName}}<i class="el-icon-download font-bold action-icon" style="color:#6612cf;margin-left: 5px" v-show="item.downIS" @click="downLoadOutPut(item)"></i></a>
                </div>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane name="all">
          <span slot="label"><i class="el-icon-tickets"></i> 知识点学习材料</span>
          <div class="row mt-3 mb-3" v-for="(item, key) in materialTypes" v-bind:key="key">
            <div class="col-md-4 col-sm-6 col-xs-12 pull-left pl-4 pr-4">
              <div class="swiper-container" style="height: 296px;" :style="{ backgroundColor: customColor(item.name) }">
                <div class="pic_left pl-6">
                  <h3 class="font-bold" style="">{{item.name}}</h3>
                  <p>{{item.describes}}</p>
                </div>
                <img :src="item.imageUrl">
              </div>
            </div>
            <div class="col-md-8 col-xs-12 pull-left">
              <div class="java_tit mb-4" :style="{ borderTopColor : customColor(item.name) }">
                <span :style="{ backgroundColor : customColor(item.name) }">{{item.name}}教程</span>
                <!--<span class="pull-right" style="color: #000000;background: #ffffff;margin-top:1px">More<i class="el-icon-caret-right"></i></span>-->
              </div>
              <div class="row pl-4 pr-5 mb-4" v-for="(material, index) in item.materialsDetail" v-bind:key="index">
                <span class="pull-left" :style="{ color: customColor(item.name) }">[{{item.name}}]</span><a class="ml-3" @click="gotoSnsMaterial(material)">{{material.materialName}}</a><a @click="gotoSnsMaterial(material)" class="pull-right font-small">查看详情</a>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!--<div class="col-md-12 mt-5 mb-6">-->
      <!--<span class="col-md-5 col-md-offset-1 font-bold">热门搜索: <span class="ml-3" v-for="(item, index) in hotSearch" v-bind:key="index">{{item}}</span></span>-->
      <!--<span class="col-md-2 col-md-offset-3 pull-left">-->
        <!--<el-input-->
        <!--placeholder="请输入内容"-->
        <!--prefix-icon="el-icon-search"-->
        <!--v-model="materialSearch">-->
        <!--</el-input>-->
      <!--</span>-->
    <!--</div>-->
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import PDF from '../../../../components/learning-library/pdf'
import { Loading } from 'element-ui'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    PDF
  },
  mounted () {
    console.log(window.innerWidth)
    this.loadData()
  },
  methods: {
    loadData () {
      let me = this
      me.projectId = localStorage.getItem('projectId')
      me.$http.post('/doproject/materialsInfoByProject', { 'sourceType': 0, 'projectId': me.projectId }, res => {
        if (res.success) {
          for (var i = 0; i < res.data.length; i++) {
            if (res.data[i].materialType === 1) {
              let obj = res.data[i]
              me.getVideoFirstImage(res.data[i].materialUrl).then(function (data) {
                obj['materialImg'] = data.data.data
                me.videoList.push(obj)
              })
            } else if (res.data[i].materialType === 0) {
              console.log(res.data[i].materialName)
              console.log(res.data[i].materialName.indexOf('_'))
              if (res.data[i].materialName.indexOf('_') === -1) {
                res.data[i].downIS = false
              } else {
                res.data[i].downIS = true
              }
              me.docList.push(res.data[i])
            }
          }
          me.$http.post('/doproject/materialsInfoByCommunity', { 'sourceType': 1 }, ressns => {
            console.log(ressns)
            me.materialTypes = ressns.data
          })
        }
      })
    },
    customColor (name) {
      if (name === 'Java') {
        return '#e66b46'
      } else if (name === 'Html5') {
        return '#18a966'
      } else {
        return '#185289'
      }
    },
    // 下载模板
    downLoadOutPut (obj) {
      const loading = Loading.service({
        lock: true,
        text: '下载中请稍候...',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)'
      })
      var nameFile = obj.materialUrl.split('/')
      var nameString = nameFile[nameFile.length - 1].split('.pdf')
      console.log(nameString[0])
      this.$axios({
        method: 'post',
        // url: 'http://39.100.37.131:8085/lying-web/doproject/output/downloadFile',
        url: 'http://www.aimaonline.cn:8085/lying-web/doproject/output/downloadFile',
        data: { 'filename': nameString[0]},
        responseType: 'blob',
        headers: {
          token: localStorage.getItem('token'),
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (response) => {
          var blob = new Blob([response.data])
          var downloadElement = document.createElement('a')
          var href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = obj.materialName
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      ).catch(
        (error) => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      )
    },
    gotoVideo (obj) {
      let me = this
      let datetime = new Date()
      datetime = me.getNowTime(datetime)
      let data = {
        'learnContent': obj.id,
        'operationTime': datetime,
        'learningState': 0
      }
      // 发送视频学习开始时间
      me.$http.post('/doproject/recordLearningTime', data, res => {
        if (res.success) {
          localStorage.setItem('materialVideo', obj.materialUrl)
          localStorage.setItem('materialId', obj.id)
          localStorage.setItem('materialName', obj.materialName)
          window.open('video-player.html', '_blank')
        }
      })
    },
    gotoPDF (obj) {
      let me = this
      if (obj.downIS === true) {
        me.$MessageBox.alert('模板无法在线预览，仅供下载使用，请点击按钮进行下载', '提示', {
          confirmButtonText: '确定'
        })
      } else {
        me.pdfFlg = false
        me.pdfOpening = obj.id
        let datetime = new Date()
        datetime = me.getNowTime(datetime)
        let data = {
          'learnContent': obj.id,
          'operationTime': datetime,
          'learningState': 0 // 开始
        }
        // 发送pdf学习开始时间
        me.$http.post('/doproject/recordLearningTime', data, res => {
          if (res.success) {
            me.$nextTick(() => {
              me.pdfUrl = obj.materialUrl
              me.pdfFlg = true
            })
          }
        })
      }
    },
    closePdf () {
      let me = this
      let datetime = new Date()
      datetime = me.getNowTime(datetime)
      let data = {
        'learnContent': me.pdfOpening,
        'operationTime': datetime,
        'learningState': 1 // 结束
      }
      // 发送学习结束时间
      me.$http.post('/doproject/recordLearningTime', data, res => {
        if (res.success) {
          // pdf页面关闭
          me.pdfFlg = false
        }
      })
    },
    getNowTime (date) {
      var Y = date.getFullYear() + '-'
      var M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) + '-' : date.getMonth() + 1 + '-'
      var D = date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' '
      var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':'
      var m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':'
      var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let dateStr = Y + M + D + h + m + s
      return dateStr
    },
    // 获取视频首帧图片
    getVideoFirstImage (vid) {
      let me = this
      let timestamp = Date.parse(new Date())
      // sign
      let sign = 'ptime=' + timestamp + '&vid=' + vid + 'TkSZYq88An'
      sign = me.$sha(sign)
      sign = sign.toLocaleUpperCase()
      return me.$axios({
        method: 'GET',
        // 查询视频观众量统计数据url
        // url: 'https://api.polyv.net/v2/data/visitor/56c20fd00b?ptime=' + timestamp + '&sign=' + sign + '&startDate=' + start + '&endDate=' + end + '&vid=' + vid,
        // 查询视频播放量统计数据接口url
        url: 'http://api.polyv.net/v2/video/56c20fd00b/get-image?ptime=' + timestamp + '&sign=' + sign + '&vid=' + vid,
        headers: {
          'Content-Type': 'application/json; charset=UTF-8'
        }
      })
    },
    gotoSnsMaterial (obj) {
      let me = this
      let datetime = new Date()
      datetime = me.getNowTime(datetime)
      let data = {
        'learnContent': obj.materialId,
        'operationTime': datetime,
        'learningState': 0 // 开始
      }
      // 发送pdf学习开始时间
      me.$http.post('/doproject/recordLearningTime', data, res => {
        console.log(res)
        window.open(obj.materialUrl + '?' + window.btoa(window.encodeURIComponent('materialId=' + obj.materialId + '&token=' + localStorage.getItem('token'))), '_blank')
      })
    },
    sizeChange (size) {
      return window.innerWidth * size / 1344 + 'px'
    }
  },
  data () {
    return {
      activeName: 'project',
      projectId: '',
      hotSearch: ['VUE', 'SpringMVC', 'Jquery', 'SpringBoot'],
      materialSearch: '',
      materialTypes: ['Java', 'Web/H5', 'C++'],
      videoList: [],
      docList: [],
      data2: [{
        name: '执行任务',
        url: 'task-list.html',
        type: '_self'
      }, {
        name: '任务清单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_blank'
      }, {
        name: '成长轨迹',
        url: 'growth-locus.html',
        type: '_self'
      }],
      pdfFlg: false,
      pdfUrl: '',
      pdfIsContrast: false,
      displayId: 'the-canvas',
      pdfOpening: '0' // 预览中的pdf
    }
  }
}
</script>
<style scoped lang="scss">
  .swiper-container {
    width: 100%;
    height: 210px;
    color: #fff;
    text-align: center;
    img{
      padding-top: 110px;
    }
  }
  .java_bg {
    background: #e66b46;
  }
  .java_color {
    color: #e66b46;
  }
  .pic_left {
    padding-bottom: 20px;
    padding-top: 10px;
    text-align: left;
    height: 90px;
    position: absolute;
    top: 0;
    left: 1rem;
    background: none;
    z-index: 10;
    padding-right: 0.4rem;
    h3{
      color:#ffffff;
      font-size: 25px
    }
    p{
      font-size: 16px;
      font-weight: 500;
    }
  }
  .java_tit {
    border-top: 2px solid #e66b46;
    span {
      padding: 10px 20px;
      display: inline-block;
      background: #e66b46;
      color: #ffffff;
    }
  }
  .line{
    float: left;
    width: 3px;
    height: 16px;
    background: #6612cf;
    overflow: hidden;
    margin-right: 5px;
  }
  .video-img{
    width: 100%;
    height: 150px;
  }
  .video-name {
    padding: 10px;
    float: left;
    width: 100%;
  }
  .video-icon{
    /*display: none;*/
    position: absolute;
    font-size: 40px;
    top: 25%;
    left: 43%;
    color:#dddddd;
    -webkit-transition: all 1.5s ease;
    transition: all 1.5s ease;
    text-shadow: 0 0 10px #ddd, 0 0 20px #ddd, 0 0 30px #666666;
  }
  .video-item:hover {
    cursor: pointer;
    .video-icon{
      display: block;
      color: #fff;
      -webkit-animation: Glow 1.5s ease infinite alternate;
      animation: Glow 1.5s ease infinite alternate;
    }
  }
  @-webkit-keyframes Glow {
    from {
      text-shadow: 0 0 10px #fff,
      0 0 20px #fff,
      0 0 30px #666666,
      0 0 40px #00a67c,
      0 0 70px #00a67c,
      0 0 80px #00a67c,
      0 0 100px #00a67c,
      0 0 150px #00a67c;
    }
    to {
      text-shadow: 0 0 5px #fff,
      0 0 10px #fff,
      0 0 15px #666666,
      0 0 20px #00a67c,
      0 0 35px #00a67c,
      0 0 40px #00a67c,
      0 0 50px #00a67c,
      0 0 75px #00a67c;
    }
  }
  @keyframes Glow {
    from {
      text-shadow: 0 0 10px #fff,
      0 0 20px #fff,
      0 0 30px #fff,
      0 0 40px #00a67c,
      0 0 70px #00a67c,
      0 0 80px #00a67c,
      0 0 100px #00a67c,
      0 0 150px #00a67c;
    }
    to {
      text-shadow: 0 0 5px #fff,
      0 0 10px #fff,
      0 0 15px #fff,
      0 0 20px #00a67c,
      0 0 35px #00a67c,
      0 0 40px #00a67c,
      0 0 50px #00a67c,
      0 0 75px #00a67c;
    }
  }
  .close-btn{
    z-index: 1001;
    position: fixed;
    top: 3%;
    left: 95%;
    color: red;
    font-size: 30px;
  }
</style>
<style>
  .el-tabs--border-card > .el-tabs__header {
    background-color: #ffffff!important;
    border-bottom: none!important;
    margin: 0;
  }
  .el-tabs--border-card {
    background: #FFFFFF;
    border: none!important;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    border-radius: 5px;
  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border: none!important;
    margin-top: -1px;
    color: #909399;
  }
  .el-tabs--border-card > .el-tabs__header .el-tabs__item {
    -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    border: 1px solid transparent;
    margin-top: -1px;
    color: #909399;
    font-size: 20px;
    padding: 20px;
  }
</style>
